.site-footer
{
  background-color:#3b83bd;
  padding:45px 0 20px;
  font-size:15px;
  line-height:24px;
  color:#fff;
}

.site-footer hr
{
  border-top-color:#bbb;
  opacity:0.5
}
.site-footer hr.small
{
  margin:20px 0
}

.site-footer h2 {
  text-transform: uppercase;
  text-align: center;
}

.site-footer h6
{
  color:#fff;
  font-size:16px;
  text-transform: uppercase;
  margin-top:5px;
  letter-spacing:2px
}