.about {
  margin: 30px auto;
  background-image: url("../../images/imagen-puerto.jpg"), linear-gradient(white, white);
  width: 100%;
}


.about-1 {
  background-color: rgba(255, 255, 255, 0.548);
  padding: 10px;
}

.about-1 h2, h3 {
  text-align: center;
  text-transform: uppercase;
  
}

h2 {
  margin-bottom: 15px;
  font-size: 40px;
}

.tarjeta-1, .tarjeta-2, .tarjeta-3{
  border: 1px solid black;
  margin: auto;
  margin-bottom: 30px;
  padding: 10px;
  height: 420px;
  width: 100%;
  border-radius: 15px;
  -webkit-box-shadow: 0px 10px 15px -4px rgba(6,103,160,0.9);
  box-shadow: 0px 10px 15px -4px rgba(6,103,160,0.9);
  display: inline-block;
  text-align: justify;
  background-color: #fff;
}

.encabezado {
  background-color: blue;
  width: 100%;
  height: auto;
  padding-bottom: 2px;
}

.symbol {
  /* pos: auto 0; */
  height: 110px;
  fill: blue;
  padding: 10px;
  display: block;
  margin: 10px auto;
  border: 1px solid black;
  border-radius: 50%;
}

@media screen and (max-width: 1200px)  {


  .tarjeta-1, .tarjeta-2, .tarjeta-3{
    height: 500px;
  }
}

@media screen and (max-width: 990px)  {
  .about {
    background: none;
  }

  .tarjeta-1, .tarjeta-2, .tarjeta-3{
    height: 400px;
  }
}

@media screen and (max-width: 500px)  {
  .tarjeta-1, .tarjeta-2, .tarjeta-3{
    height: 450px;
  }
}

@media screen and (max-width: 400px)  {
  .about-1 h2 {
    font-size: 30px;
  } 

  .tarjeta-1, .tarjeta-2, .tarjeta-3{
    height: 500px;
  }
}